@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300&family=PT+Sans&family=Playfair+Display:wght@700&family=Poppins:ital,wght@0,300;1,300&family=Roboto&family=Sofia+Sans+Semi+Condensed:wght@200&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


/* font-family: 'Noto Sans', sans-serif; */


/* font-family: 'Playfair Display', serif; */


/* //navbar css */

.navbar {
    font-family: "Poppins", sans-serif;
    font-size: 17px;
    line-height: 26px;
    text-transform: uppercase;
    box-shadow: 0 8px 6px -6px gray;
    --webkit-box-shadow: 0 8px 6px -6px gray;
    border-bottom: solid gray !important;
}


/* .nav-link {
