.active {
    border-bottom: 2px solid black;
} */

.navbar-brand {
    font-weight: 700;
    font-family: "roboto", sans-serif;
    letter-spacing: 3px;
}

.search-form {
    margin-right: 10px;
    margin-top: 4px;
}

.search-form input {
    border: none;
    border-radius: 0;
}

.search-form button {
    background-color: #000000;
    border-radius: 0;
    color: white;
}


/* ***************
Page not found page degine
 */

.pnf {
    display: flex;
    min-height: 65vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.pnf-title {
    font-size: 100px;
    font-weight: 700;
}

.pnf-heading {
    font-weight: normal;
}

.pnf-btn {
    border: 1px solid black;
    color: black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
}

.pnf-btn:hover {
    background-color: black;
    color: white;
}


